import React, { Component } from "react";
import LocationDataService from "../services/location.service";
import AuthService from "../services/auth.service";

export default class Location extends Component {
  constructor(props) {
    super(props);
    this.onChangeTitle = this.onChangeTitle.bind(this);
    this.onChangeDescription = this.onChangeDescription.bind(this);
    this.getLocation = this.getLocation.bind(this);
    this.updatePublished = this.updatePublished.bind(this);
    this.updateLocation = this.updateLocation.bind(this);
    this.deleteLocation = this.deleteLocation.bind(this);

    this.state = {
      currentLocation: {
        id: null,
        title: "",
        description: "",
        published: false,
        authorId:null
      },
      message: "",
      loggedInUser: null,
      isLoggedIn:false,
      isMod: false,
      isAdmin: false,
    };
  }

  componentDidMount() {
    const loggedInUser = AuthService.getCurrentUser();

    if (loggedInUser) {
      this.setState({
        loggedInUser: loggedInUser.id,
        isLoggedIn:true,
        isMod: loggedInUser.roles.includes("ROLE_MODERATOR"),
        isAdmin: loggedInUser.roles.includes("ROLE_ADMIN"),
      });
    }
    this.getLocation(this.props.match.params.id);
  }

  onChangeTitle(e) {
    const title = e.target.value;

    this.setState(function(prevState) {
      return {
        currentLocation: {
          ...prevState.currentLocation,
          title: title
        }
      };
    });
  }

  onChangeDescription(e) {
    const description = e.target.value;
    
    this.setState(prevState => ({
      currentLocation: {
        ...prevState.currentLocation,
        description: description
      }
    }));
  }

  getLocation(id) {
    LocationDataService.get(id)
      .then(response => {
        this.setState({
          currentLocation: response.data
        });
        console.log(response.data);
      })
      .catch(e => {
        console.log(e);
      });
  }

  updatePublished(status) {
    var data = {
      id: this.state.currentLocation.id,
      title: this.state.currentLocation.title,
      description: this.state.currentLocation.description,
      published: status,
      authorId:this.state.currentLocation.authorId,
      loggedInUser:this.state.loggedInUser
    };

    LocationDataService.update(this.state.currentLocation.id, data)
      .then(response => {
        this.setState(prevState => ({
          currentLocation: {
            ...prevState.currentLocation,
            published: status
          },
          message : response.data.message
        }));
        this.getLocation(this.state.currentLocation.id);
      })
      .catch(e => {
        console.log(e);
        this.getLocation(this.state.currentLocation.id);
      });
  }

  updateLocation() {
    LocationDataService.update(
      this.state.currentLocation.id,
      this.state.currentLocation
    )
      .then(response => {
        console.log(response.data);
        this.setState({
          message: response.data.message
        });
        this.getLocation(this.state.currentLocation.id);
      })
      .catch(e => {
        console.log(e);
        this.getLocation(this.state.currentLocation.id);
      });
  }

  deleteLocation() {    
    LocationDataService.delete(this.state.currentLocation.id)
      .then(response => {
        console.log(response.data);
        this.props.history.push('/locations')
      })
      .catch(e => {
        console.log(e);
      });
  }

  render() {
    const { currentLocation } = this.state;

    return (
      <div>
        {currentLocation ? (
          <div className="edit-form">
            <h4>Location</h4>
            <form>
              <div className="form-group">
                <label htmlFor="title">Title</label>
                <input
                  type="text"
                  className="form-control"
                  id="title"
                  value={currentLocation.title}
                  onChange={this.onChangeTitle}
                />
              </div>
              <div className="form-group">
                <label htmlFor="description">Description</label>
                <input
                  type="text"
                  className="form-control"
                  id="description"
                  value={currentLocation.description}
                  onChange={this.onChangeDescription}
                />
              </div>

              <div className="form-group">
                <label>
                  <strong>Status:</strong>
                </label>
                {currentLocation.published ? "Published" : "Pending"}
              </div>
            </form>

            
            {(this.state.loggedInUser == currentLocation.authorId || this.state.isMod || this.state.isAdmin) && (
                <button
                  className="badge badge-primary mr-2"
                  onClick={() => this.updatePublished(!currentLocation.published)}
                >
                  {currentLocation.published ? ("UnPublish") : ("Publish") }
                </button>
               )} 

                {(this.state.loggedInUser == currentLocation.authorId || this.state.isMod || this.state.isAdmin) && (
                <button
                  type="submit"
                  className="badge badge-success mr-2"
                  onClick={this.updateLocation}
                >
                  Update
                </button>
                )}



            {this.state.isAdmin && (
                <button
                  className="badge badge-danger"
                  onClick={this.deleteLocation}
                >
                  Delete
                </button>
              )}


            <p>{this.state.message}</p>
          </div>
        ) : (
          <div>
            <br />
            <p>Please click on a Location...</p>
          </div>
        )}
      </div>
    );
  }
}